.dialogs{
    display: grid;
    grid-template-columns: 2fr 10fr;
    margin-left: 10px;
    margin-top: 20px;
}
.dialogsItems{
    padding: 10px;
    border-right: 1px solid green;
    margin-right: 30px;
}
.messages{
    padding: 10px;
}
.messages textarea{
    border: 1px solid rgb(44, 111, 199);
    border-radius: 5px;
    margin-right: 15px;
    font-size: 20px;
}
.messages button{
    padding: 10px 45px;
    background: rgb(216, 21, 119);
    border: none;
    border-radius: 5px;
    margin: auto;
    box-shadow: 1px 2px 3px rgb(44, 111, 199);
    cursor: pointer;
}
.dialog{
    font-size: 25px;
    margin-top: 10px;
    margin-right: 30px;
}
.dialog a{
    color: black !important;
    text-decoration: none;
    
}